<template>
  <div class="check-in">
    <form>
      <div class="form-group">
        <label>口令：</label>
        <input placeholder="签到时请忽略" type="password" v-model="command" class="form-control" />
      </div>
      <div class="form-group">
        <label>学号：</label>
        <input placeholder="请输入学号" type="text" v-model="id" class="form-control"/>
      </div>
      <div class="form-group">
        <label>姓名：</label>
        <input placeholder="请输入姓名" type="text" v-model="name" class="form-control"/>
      </div>
      <div class="form-group">
        <label for="scheduleList">时间：</label>
        <select id="scheduleList" v-model="schedule" class="form-control">
          <option v-for="(s,si) in scheduleList" :value="s" :key="s" :selected="si==0">{{s}}</option>
        </select>
      </div>
      <div class="form-group">
        <label for="courseList">课程：</label>
        <select id="courseList" v-model="course" class="form-control">
          <option v-for="(c,ci) in courseList" :value="c" :key="c" :selected="ci==0">{{c}}</option>
        </select>
      </div>
      <div class="form-group">
        <button
          class="btn btn-primary btn-lg form-control"
          @click.prevent="handleClickCheckin"
          :disabled="!formfilled || (remaining>0)"
        >
          签到
          <span v-show="remaining>0">({{remaining}})</span>
        </button>
      </div>
    </form>
    <table class="table">
      <thead>
        <tr>
          <th>学号</th>
          <th>姓名</th>
          <th>时间</th>
          <th>课程</th>
          <th v-if="command!=5641">签到</th>
          <th v-else>删除</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(r,ri) in sortedRecords" :key="ri">
          <td>{{r.id}}</td>
          <td>{{r.name}}</td>
          <td>{{r.course}}</td>
          <td>{{r.courseid}}</td>
          <td v-if="command!=5641">{{new Date(r.checktime).toLocaleString()}}</td>
          <td v-else>
            <van-icon name="clear" color="red" size="40px" @click="handleDeleteRecord(r._id)"></van-icon>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>
<script>
export default {
  data: function() {
    return {
      lastcheck: 0,
      handle: null,
      remaining: 0,
      command: "",
      id: "",
      name: "",
      schedule: "周一上午",
      scheduleList: [
        "周一上午",
        "周一下午",
        "周一晚上",
        "周二上午",
        "周二下午",
        "周二晚上",
        "周三上午",
        "周三下午",
        "周三晚上",
        "周四上午",
        "周四下午",
        "周四晚上",
        "周五上午",
        "周五下午",
        "周五晚上",
        "周六上午",
        "周六下午",
        "周六晚上",
        "周日上午",
        "周日下午",
        "周日晚上"
      ],
      course: "4-3,4-4 光速测量",
      courseList: ["4-3,4-4 光速测量", "6-1 核磁共振", "6-3 光泵磁共振"],
      records: []
    };
  },
  computed: {
    formfilled: function() {
      return this.id && this.name && this.schedule && this.course;
    },
    sortedRecords: function() {
      return this._.cloneDeep(this.records).sort((a, b) => {
        return b.checktime - a.checktime;
      });
    }
  },
  created: function() {
    this.lastcheck = parseInt(localStorage.getItem("lastcheck")) || 0;
    this.countLastcheck();
    this.getRecords();
  },
  methods: {
    getRecords: function() {
      this.$http.get("http://hosteons.nutbrain.tk:3000/all").then(res => {
        if (res.body.success) {
          this.records = res.body.records;
        }
      });
    },
    handleClickCheckin() {
      let body = {
        id: this.id,
        name: this.name,
        course: this.schedule,
        courseid: this.course,
        checktime: Date.now()
      };
      this.$http
        .post("http://hosteons.nutbrain.tk:3000/checkin", body)
        .then(res => {
          window.console.log(res);
          if (res.body.success) {
            this.$toast.success("签到成功");
            this.id = "";
            this.name = "";
            this.lastcheck = Date.now();
            localStorage.setItem("lastcheck", this.lastcheck.toString());
            this.countLastcheck();
            this.getRecords();
          } else {
            this.$toast.fail("签到失败");
          }
        });
    },
    handleDeleteRecord(_id) {
      this.$http
        .get("http://hosteons.nutbrain.tk:3000/delete?_id=" + _id)
        .then(res => {
          if (res.body.success) {
            this.$toast.success("删除成功");
            this.getRecords();
          } else {
            this.$toast.fail("删除失败");
          }
        });
    },
    countLastcheck() {
      this.handle = setInterval(() => {
        this.remaining = Math.ceil((this.lastcheck - Date.now()) / 1000) + 60;
      }, 1000);
      if (this.remaining < 0) {
        clearInterval(this.handle);
      }
    }
  }
};
</script>
