  display: flex;
  align-items: center;

  justify-content: space-between;
  margin: 10px auto;
}
label{
    min-width:2em;
}
input,
select {
  height: 2em;
  flex: 1;
  border: none;
  border-bottom: 1px solid black;
  outline: none;
  text-align: center;
  text-align-last: center;
  background: none;
  border-radius: 0;
}
th {
  width: 20%;
}
table {
  margin-bottom: 50px;
}
</style>